/* Modal styles */

.preview-modal {
  display: -ms-flexbox;
  display: flex;
  top: 0;
  left: 0;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(153, 153, 153, .8);
  z-index: 100;
}

.preview-modal__body {
  width: 50vw;
  padding: 1px 23px 30px;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
}

.preview-modal__buttons {
  -ms-flex-pack: end;
      justify-content: flex-end;
}



